.card{
	display: flex;
	flex-direction: column;
	width: 90%;
	max-width: 700px;
	height: 100%;	
	border: 1px solid rgb(79, 79, 221);
	border-radius: 5px;
	margin: 5px 15px;
	padding: 5px 10px;
	justify-content: space-between;
}

@media(min-width: 620px) {
	.card{
		margin: 20px 15px;
	}
}

@media (max-width: 470px) {
	.card{
		padding: 5px 5px;
	}
}