.wrapper{
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: white;
	width: 760px;
	margin: 50px 0;
	height: 80%;
	padding-top: 10px;
	border-radius: 5px;
	overflow: auto;
}

.wrapper h2{
	font-size: 4rem;
}

@media (max-width: 982px) {
	.wrapper{
		width: 620px;
	}

	.wrapper h2{
		font-size: 3.5rem;
	}
}

@media (max-width: 766px) {
	.wrapper{
		width: 550px;
	}

	.wrapper h2{
		font-size: 3rem;
	}
}

@media (max-width: 650px) {
	.wrapper{
		width: 400px;
	}

	.wrapper h2{
		font-size: 2.5rem;
	}
}

@media (max-width: 470px) {
	.wrapper{
		width: 280px;
	}

	.wrapper h2{
		font-size: 2rem;
	}
}