.backdrop{
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	z-index: 5;
	background-color: rgba(0, 0, 0, 0.85);
}

.content{
	background-color: white;
	width: 500px;
	height: 300px;
	top: 30%;
	left: 34%;
	z-index: 6;
	position: absolute;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	padding: 10px 25px;
	font-size: 24px;
}

.content form{
	display: flex;
	flex-direction:  column;
}

.title{
	font-size: 32px;
}

label{
	padding-right: 10px;
}
input{
	border: 1px solid black;
	padding: 5px
}

.control{
	display: flex;
	justify-content: space-around;
}